<style lang="stylus" type="text/stylus">
  #headerPage {
    position: absolute
    top: 0
    left: 0
    right: 0
    height: 40px
    .ivu-menu-horizontal {
      height: 40px
      line-height: 40px
    }
    .log-in {
      float: right
    }
  }
</style>

<template>
  <div id="headerPage">
    <Menu mode="horizontal" @on-select="selNav" active-name="homePage">
      <MenuItem name="homePage">
        <Icon size="18" type="ios-home"></Icon>
        Home
      </MenuItem>

      <MenuItem name="journal">
        <Icon size="18" type="ios-book"></Icon>
        Journal
      </MenuItem>

      <MenuItem name="chart">
        <span v-if="$store.state.user"><Icon size="18" type="earth"></Icon>
        Chart</span>

      </MenuItem>

      <MenuItem v-if="!$store.state.user" class="log-in" name="login">
        <Icon type="log-in"></Icon>
      </MenuItem>
      <MenuItem class="log-in" v-else name="logout">
        <Icon type="log-out"></Icon>
      </MenuItem>
    </Menu>
  </div>
</template>

<script type="text/ecmascript-6">

export default {
  name: 'headerPage',
  data() {
    return {};
  },
  computed: {},
  methods: {
    selNav(name) {
      if (name === 'login') {
        this.$store.commit('setLoginModal', true);
      } else if (name === 'logout') {
        this.$store.commit('setUser', null);
        this.$cookie.delete('TWFuaWFjVXNlcg');
        this.$router.push({
          name: 'home',
        });
      } else {
        this.$router.push({
          name,
        });
      }
    },
  },
  mounted() {},
  created() {},
};
</script>
